import React from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
export default function Works({ location }) {
  function updatePagination(data) {
    const cursor = document.getElementById("cursor");
    const follow = document.getElementById("follow");
    const addTransformer = () => {
      if (cursor) {
        cursor.classList.add("link");
      }
      if (follow) {
        follow.classList.add("link");
      }
    };
    const removeTransformer = () => {
      if (cursor) {
        cursor.classList.remove("link");
      }
      if (follow) {
        follow.classList.remove("link");
      }
    };
    data.Components.Pagination.items.forEach((item) => {
      item.button.innerHTML = `<span></span>`;
      item.button.addEventListener("mouseenter", addTransformer);
      item.button.addEventListener("mouseleave", removeTransformer);
    });
  }
  return (
    <div>
      <Splide
        onPaginationMounted={updatePagination}
        options={{
          direction: "ttb",
          height: "calc(100vh - 60px)",
          arrows: false,
          perPage: 1,
          wheel: false,
          drag: false,
          releaseWheel: true,
          speed: 1000,
          start: location?.hash?.slice(1) || 1,
        }}
      >
        <SplideSlide>1</SplideSlide>
        <SplideSlide>2</SplideSlide>
        <SplideSlide>3</SplideSlide>
        <SplideSlide>4</SplideSlide>
        <SplideSlide>5</SplideSlide>
      </Splide>
    </div>
  );
}
